// @import-normalize;
@import 'helper';
 *{
     margin: 0;
     padding: 0;
 }
 *{
     box-sizing: border-box;
 }
 *::after{
    box-sizing: border-box;
 }
 *::before{
    box-sizing: border-box;
 }
 ul,ol{
list-style: none;
 }
 a{
   text-decoration: none;
   color: inherit;
 }
 body{
  font-family: $font-hei;
  font-size: 16px;
  max-width: 500px;
  margin: 0 auto;
  max-height:800px;
  background:rgba(163, 163, 214, 0.1);
  // background: #f5f5f5;
  // background:rgba(90, 92, 201, 0.3)
 }
 .icon{
   display: inline-block;
 }
 button{
   font-family: inherit;
 }
 :focus{
   outline: none;
 }
 .clearfix::after{
   content: '';
   display: block;
   clear: both;
 }